.label{
    display: flex;
    align-items: center;
    position: relative;

    input{
        padding-left: 25px;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }
}

.icon {
    width: 22px;
    height: 22px;
    opacity: 1;
    position: absolute;
    left: 2px;
    top: -1px;
}