* {
    padding: 0;
    margin: 0;
    list-style: none;
    outline: none;
    font-family: 'Nunito', Roboto, system-ui, Tahoma, sans-serif;
    box-sizing: border-box;
}
html{
    display: block;
}
html,body{
    height: 100%;
    overflow: hidden;
}

body{
    background-color: #6a5be2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.App{
    margin: 0 auto;
    padding: 20px 20px 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    min-height: 100%;
    overflow-x: hidden;
    
}