.body{
    height: calc(100vh - 100px);
}

.ul{
    font-size: 30px;
    li{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-between;

        p:first-child{
            width:100%;
            display: flex;
            justify-content: center;
        }

        p{
            display: flex;
            align-items: center;
        }

        button {
            margin-left: 20px;
            border-radius: 50%;
            background-color: #293449;
            height: 50px;
            width: 50px;
            border: 0;
            font-size: 37px;
            color: #ffffff;
            rotate: 45deg;
        }
    }
}